import React from 'react'
import './styles/MateriStyle.css'

export default class Materi extends React.Component {

	constructor(props){
		super(props)
		this.state = {
			'title': [
				'HTML/CSS',
				'C++',
				'JAVASCRIPT',
				'JAVA',
				'GO',
				'PYTHON',
				'PHP',
				'RUBY'
			]
		}
	}

	render() {
		return(
			<div className="section">
                <div className="materiContainer">
                    <div className="title">
                        Materi
                    </div>
                    <div className="content">
						{
							this.state.title.map((a, b) => {
								return(
									<Card title={a} key={b}/>
								)
							})
						}
                    </div>
                </div>
			</div>
		)
	}
}

class Card extends React.Component {
	render() {
		return(
            // eslint-disable-next-line
            <a href="#" className="materiCardContainer" target="_blank">
				{this.props.title}
			</a>
		)
	}
}



