import React from 'react'
import './styles/ContactStyle.css'

export default class Contact extends React.Component {
    render() {
	return(
	    <div className="section">
    		<div className="contactContainer">
                <div className="formContainer">
                    <div className="title">
                        Email Us!!
                    </div>
                    <form action="mailto:arganaphangquestian@gmail.com">
                        <input className="inputText" type="text" placeholder="From"/>
                        <input className="inputText" type="text" placeholder="Subject"/>
                        <input className="inputText" type="text" placeholder="What do you want?"/>
                        <input className="inputSubmit" type="submit" value="Send It to Us!"/>
                    </form>
                </div>
                <div className="footer">
                    copyright &copy; all reserved by Argana Phangquestian
                </div>
    		</div>
	    </div>
	)
    }
}



