import React from 'react'
import './styles/AboutUsStyle.css'

export default class AboutUs extends React.Component {

    constructor(props){
        super(props)
        this.state = {
                "title": [
                    "Web Development",
                    "Robotic",
                    "ISC",
                    "Network",
                    "Android Development",
                    "Game Development"
                ],
                "description": [
                    "Web Development dolor sit amet, consectetur adipiscing elit. In porttitor ac diam mattis laoreet. Ut congue est sed quam bibendum, eget mattis ipsum blandit. Class aptent taciti sociosqu ad litora torquent per conubia nostra, per inceptos himenaeos. Vivamus tempor ac lorem et scelerisque. Donec volutpat iaculis elit eget sodales. In porta turpis ut tempor luctus.",
                    "Robotic dolor sit amet, consectetur adipiscing elit. In porttitor ac diam mattis laoreet. Ut congue est sed quam bibendum, eget mattis ipsum blandit. Class aptent taciti sociosqu ad litora torquent per conubia nostra, per inceptos himenaeos. Vivamus tempor ac lorem et scelerisque. Donec volutpat iaculis elit eget sodales. In porta turpis ut tempor luctus.",
                    "ISC ipsum dolor sit amet, consectetur adipiscing elit. In porttitor ac diam mattis laoreet. Ut congue est sed quam bibendum, eget mattis ipsum blandit. Class aptent taciti sociosqu ad litora torquent per conubia nostra, per inceptos himenaeos. Vivamus tempor ac lorem et scelerisque. Donec volutpat iaculis elit eget sodales. In porta turpis ut tempor luctus.",
                    "Network dolor sit amet, consectetur adipiscing elit. In porttitor ac diam mattis laoreet. Ut congue est sed quam bibendum, eget mattis ipsum blandit. Class aptent taciti sociosqu ad litora torquent per conubia nostra, per inceptos himenaeos. Vivamus tempor ac lorem et scelerisque. Donec volutpat iaculis elit eget sodales. In porta turpis ut tempor luctus.",
                    "Android Development dolor sit amet, consectetur adipiscing elit. In porttitor ac diam mattis laoreet. Ut congue est sed quam bibendum, eget mattis ipsum blandit. Class aptent taciti sociosqu ad litora torquent per conubia nostra, per inceptos himenaeos. Vivamus tempor ac lorem et scelerisque. Donec volutpat iaculis elit eget sodales. In porta turpis ut tempor luctus.",
                    "Game Development dolor sit amet, consectetur adipiscing elit. In porttitor ac diam mattis laoreet. Ut congue est sed quam bibendum, eget mattis ipsum blandit. Class aptent taciti sociosqu ad litora torquent per conubia nostra, per inceptos himenaeos. Vivamus tempor ac lorem et scelerisque. Donec volutpat iaculis elit eget sodales. In porta turpis ut tempor luctus."
                ],
                "currentCard": 0,
                "active": "active",
                "transition": null
            }
    }

    handleButtonClick = (key) => {
        this.setState({
            currentCard: key,
        })
        //console.log(key)
        
        if(key !== this.state.currentCard) {
            this.setState({
                transition: "hide"
            })
            setTimeout(() => {
                this.setState({
                    transition: null
                })
            }, 300)
        } else {
            console.log("Nooo")
        }
    }

    render() {
        return(
            <div className="section">
                <div className="aboutusContainer">
                    <div className={["aboutusCardContainer", this.state.transition].join(' ')}>
                        <div className="aboutusTitleContent">
                            {this.state.title[this.state.currentCard]}
                        </div>
                        <div className="aboutusDescriptionContent">
                            {this.state.description[this.state.currentCard]}
                        </div>
                    </div>
                </div>
                <div className="aboutusContentNavigation">
                    <ul>
                        {
                            this.state.title.map((a, b) => {
                                return (
                                    <li className={a === this.state.title[this.state.currentCard] ? this.state.active : null} key={b}>
                                        <button onClick={() => {this.handleButtonClick(b)}}>
                                            {a}
                                            <span></span>
                                        </button>
                                    </li>
                                )
                            })
                        }
                    </ul>
                </div>
            </div>
        )
    }
}



